html {
  box-sizing: border-box;
  font-size: 100%;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: Inter, sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
}

:root h1 {
  font-weight: 500;
  margin: 0px;
}
:root h2 {
  font-size: 2.8rem;
  margin-bottom: 59px;
}
:root h3 {
  font-size: 1.68rem;
  font-weight: 800;
  margin: 0px;
}
:root h4 {
  font-weight: 600;
  font-size: 2rem;
  text-align: center;
}
:root p {
  font-weight: 500;
  line-height: 1.5rem;
  margin: 0px;
}
:root a {
  cursor: pointer;
  text-decoration: none;
  color: var(--text);
}
:root strong {
  font-weight: 900;
}

@media (min-width: 20em) {
  h1 {
    font-size: 1.75rem;
  }
}
@media (min-width: 44.6em) {
  h1 {
    font-size: 2.5rem;
  }
}
@media (min-width: 62.3em) {
  h1 {
    font-size: 2.65rem;
  }
}
:root {
  --white: rgb(255, 255, 255);
  --black: hsl(0, 0%, 12%);
  --brown: hsl(29, 30%, 61%);
  --light-brown: hsl(29, 63%, 82%);
  --sage: hsl(138, 21%, 88%);
  --light-yellow: hsl(40, 43%, 96%);
  --yellow: #FFB802;
  --background-color: var(--white);
  --text-color: var(--black);
  --buttons: var(--white);
  --buttons-hover: var(--black);
}

@keyframes fadeIn {
  from {
    top: 0;
  }
  to {
    top: -10px;
  }
}
@keyframes wobble {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0px);
  }
}
:root h3 {
  font-size: 1.68rem;
  font-weight: 800;
}
:root h2 {
  color: var(--yellow);
  text-align: left;
  margin: 0px;
}
:root p {
  padding: 20px 0px;
}

.hidden-animate {
  opacity: 0;
  transition: all 1s;
  transform: translateY(30%);
}

.show {
  opacity: 1;
  transform: translateY(0);
}

.container-popup {
  display: none;
  width: 100%;
  height: 100%;
  z-index: 10000;
  position: fixed;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
}
.container-popup img {
  object-fit: scale-down;
  max-width: 100%;
}
.container-popup span {
  position: absolute;
  top: 0;
  right: 30px;
  font-size: 4rem;
  font-weight: 500;
  color: white;
  cursor: pointer;
  text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.8);
}

.socials {
  display: flex;
  gap: 14px;
}

picture {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 88px;
  gap: 1%;
}
picture > * {
  display: inline;
  width: 100%;
  border-radius: 10%;
}
picture .hidden {
  display: none;
}

li {
  list-style-position: inside;
}

body {
  background-image: url(/public/assets/backgroundmobile.png);
  background-repeat: no-repeat;
  background-position: top;
  background-position-y: 60px;
}

header {
  z-index: 10;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--white);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  padding: 22px;
}
header .mobile-nav-toggle {
  width: 40px;
  background: red;
  aspect-ratio: 1;
  z-index: 9000;
  background: url(/public/assets/menu.png);
  background-repeat: no-repeat;
  background-size: contain;
  border: 0;
}
header .mobile-nav-toggle:hover {
  cursor: pointer;
}
header .mobile-nav-toggle[aria-expanded=true] {
  background: url(/public/assets/cross.png);
  background-repeat: no-repeat;
  background-size: contain;
}
header .logo {
  display: none;
}
header .primary-navigation {
  z-index: 200;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  inset: 0 0 0 0%;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(12px);
  font-size: 2rem;
  transform: translateX(100%);
  transition: transform 350ms ease-out;
}
header .primary-navigation a {
  padding: 10px 20px;
  border: 2px solid transparent;
  border-radius: 84px;
  transition: 0.4s;
}
header .primary-navigation a:hover {
  padding: 10px 26px;
  border: 2px solid var(--black);
  border-radius: 84px;
}
header .primary-navigation .active {
  padding: 10px 26px;
  border: 2px solid var(--black);
  border-radius: 84px;
}
header .primary-navigation[data-visible=true] {
  transform: translateX(0%);
}
header .menu {
  width: 45px;
}

.onlydesktop {
  display: none;
}

.onlymobile {
  z-index: 50;
  width: 100%;
  position: sticky;
  top: 88px;
}
.onlymobile div {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: auto;
  padding: 25px 20px;
  gap: 2.5rem;
  background-color: var(--white);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  -ms-overflow-style: none;
  /* for Internet Explorer, Edge */
  scrollbar-width: none;
  /* for Firefox */
  overflow-x: scroll;
}
.onlymobile div a {
  white-space: nowrap;
}
.onlymobile div::-webkit-scrollbar {
  display: none;
}
.onlymobile div a:hover {
  font-weight: 700;
  transition: 0.2s;
}
.onlymobile div .active {
  font-weight: 700;
}

svg {
  fill: var(--black);
  transition: 0.4s;
}
svg:hover {
  fill: var(--light-brown);
}

table {
  max-width: 22rem;
}
table td:first-child {
  white-space: nowrap;
  padding-right: 0.625rem;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

main {
  position: relative;
  z-index: 3;
  display: flex;
  flex-direction: column;
  padding: 40px 20px 120px 20px;
  gap: 80px;
  max-width: 70rem;
}
main ul {
  padding-left: 0;
  line-height: 24px;
}
main img {
  -ms-overflow-style: none;
  /* for Internet Explorer, Edge */
  scrollbar-width: none;
  /* for Firefox */
  overflow-x: scroll;
  width: 100%;
}

.firstIteration,
.secondIteration {
  display: flex;
  flex-direction: column;
  gap: 32px;
  max-width: 17rem;
  margin: auto;
}

.thirdIteration {
  margin: auto;
  max-width: 250px;
}

section {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
section h3 {
  padding-top: 42px;
}

footer {
  background-color: var(--white);
  box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.25);
  padding: 33px 0px 33px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}

@media (min-width: 44.6em) {
  body {
    background-image: url(/public/assets/backgroundmedium.png);
  }
  .container-popup img {
    padding: 60px 0px;
  }
  header {
    justify-content: space-between;
  }
  header .menu {
    display: none;
  }
  header .mobile-nav-toggle {
    display: none;
  }
  header .primary-navigation {
    position: unset;
    display: inline;
    background-color: transparent;
    inset: unset;
    font-size: 1.2rem;
    font-weight: 500;
    transform: unset;
  }
  header .primary-navigation a {
    padding: 10px 20px;
    border: 2px solid var(--white);
    border-radius: 84px;
    transition: 0.4s;
  }
  header .primary-navigation a + a {
    margin-left: 40px;
  }
  header .primary-navigation a:hover {
    padding: 10px 26px;
    border: 2px solid var(--black);
    border-radius: 84px;
  }
  .onlymobile {
    display: none;
  }
  .onlydesktop {
    display: block;
    position: relative;
    min-width: 13.5rem;
    padding-top: 50px;
  }
  .onlydesktop div {
    position: sticky;
    top: 130px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 11rem;
    margin: auto;
    padding: 25px 20px;
    gap: 17px;
    background-color: var(--white);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
  }
  .onlydesktop div a:hover {
    font-weight: 700;
    transition: 0.2s;
  }
  .onlydesktop div .active {
    font-weight: 700;
  }
  picture {
    padding-top: 100px;
  }
  picture > * {
    display: inline;
    width: 45%;
    border-radius: 10%;
  }
  picture .hidden:nth-child(1) {
    display: inline;
  }
  .firstIteration,
  .secondIteration {
    display: flex;
    flex-direction: row;
    gap: 32px;
    max-width: 45rem;
    margin: auto;
    padding: 20px 0px;
    align-items: center;
    justify-content: center;
  }
  .firstIteration .two,
  .firstIteration .three,
  .secondIteration .two,
  .secondIteration .three {
    width: 12rem;
  }
  .thirdIteration {
    margin: auto;
    max-width: unset;
  }
  .thirdIteration img {
    width: fit-content;
    max-width: 12rem;
  }
  footer {
    flex-direction: row;
    justify-content: center;
    gap: 130px;
  }
}
@media (min-width: 62.3em) {
  body {
    background-image: url(/public/assets/backgroundlarge.png);
  }
  header {
    justify-content: space-between;
  }
  header .logo {
    display: inline;
    width: 54px;
  }
  .hero-container .hero {
    flex-direction: row;
    align-items: center;
    gap: 20px;
  }
  .hero-container .hero .profile-img {
    order: 1;
    width: 309px;
    height: 309px;
  }
  .hero-container .hero .hero-text {
    order: 0;
  }
  .userPersona {
    display: flex;
  }
  picture > * {
    display: inline;
    width: 31%;
    border-radius: 10%;
  }
  .hidden:nth-child(2) {
    display: inline;
  }
  .firstIteration-container {
    display: flex;
    gap: 20px;
  }
  .firstIteration-container .text {
    max-width: 400px;
  }
  .pro-con {
    display: flex;
    gap: 6.25rem;
  }
}